import React, { Component } from 'react';
import axios from 'axios';

class User extends Component {
    state = {
        data: []
    }
    componentWillMount() {
        axios.get(`https://jsonplaceholder.typicode.com/users?id=${this.props.id}`)
            .then(response => this.setState({data: response.data[0]}))
    }
    render() {
        return(
            <div className='item'>
                <p>Name: {this.state.data.name}</p>
                <p>Username: {this.state.data.username}</p>
                <p>Email: {this.state.data.email}</p>
                <p>Phone: {this.state.data.phone}</p>
                <p>Website: {this.state.data.website}</p>
            </div>
        )
    }
}

export default User;


